	module.exports = {"data":{"contentfulChapitre":{"titreChapitre":"Chapitre 10 - Éléa","texte":{"childMarkdownRemark":{"html":"<p>Une fois que les gardes m’ont reconduit dans ma chambre, je remarque que mon ordinateur a été retiré. Sérieux, ça ne suffisait pas de m’enfermer dans une chambre d’isolement? Ils devaient vraiment me retirer mon ordinateur? Je vais finir par tuer quelqu’un merde! Dès que je revois Darakei, je le tue, je le décapite, je lui ouvre le corps et je lui retire tous ses organes! Je serre les poings. Je dois me calmer. Inspire... Expire... Inspire... Expire... Ce n’est pas bon pour mon corps si je m’énerve trop. Je regarde vers ma porte de chambre et pose ma main sur la porte.</p>\n<p>Wamien: Je veux sortir d’ici.<br>\nVoix féminine: Je peux t’aider si tu veux?<br>\nWamien: Qui? Comment?</p>\n<p>Je regarde autour de moi, j’étais seul dans ma chambre et la voix ne peut pas venir du couloir.</p>\n<p>Éléa: Je suis Éléa, j’ai le pouvoir de devenir invisible.<br>\nWamien: Heum, d’accord? Mais comment tu veux m’aider? Je ne peux pas être invisible aussi...<br>\nÉléa: Je connais le bâtiment par cœur, je peux te conduire à la sortie en cachant les caméras sans problème.<br>\nWamien: Je, je ne sais pas quoi dire.<br>\nÉléa: Dire merci, ça serait un bon début.<br>\nWamien: Merci beaucoup, on y va?<br>\nÉléa: Laisse-moi cinq minutes, je reviens te chercher.<br>\nWamien: D’accord.</p>\n<p>La porte ondule un peu comme si quelque chose passe au travers et puis elle redevient normale. Quelqu’un est venu me secourir. La porte s’ouvre un peu. Je m’avance vers elle et l’ouvre un peu.</p>\n<p>Éléa: La voie est libre, suis-moi.<br>\nWamien: Je ne te vois pas, tu sais?<br>\nÉléa: Tu as une vision thermique non?<br>\nWamien: Heum ouais, désolé.</p>\n<p>J’active ma vision thermique et la suit. Au tournant, elle me fait signe de m’arrêter. Elle regarde de l’autre côté du couloir.</p>\n<p>Éléa: Je vais couvrir la caméra de surveillance, j’y vais reste bien derrière moi.<br>\nWamien: Caméra du côté supérieur gauche ou droit?<br>\nÉléa: Droit pourquoi?</p>\n<p>Je me place contre le mur et passe mon petit doigt sur le rebord du mur et tire un brouilleur à proximité. Des petits arcs électriques apparaissent autour de la caméra et puis elle descend, brisée.</p>\n<p>Wamien: Voilà pourquoi, allez on y va.<br>\nÉléa: Reste derrière moi quand même...</p>\n<p>Elle me fait passer dans les couloirs les plus insignifiants de tout l’édifice et on finit par aboutir à la porte de sortie de service. Elle ouvre la porte et sort, je la suis. Elle referme la porte derrière moi.</p>\n<p>Wamien: Et mes amis? Tom, Sam, Tiana? Que vont-ils devenir sans moi?<br>\nÉléa: Je Heum, je ne peux pas aider pour ça... Je suis désolée. Trouve-toi un endroit où vivre hors du royaume et évite tout le monde, oh! Et...</p>\n<p>Elle sortit un petit appareil qu’elle plaça autour de mon poignet. Elle appuya sur un bouton et je sentis un choc électrique parcourir mon poignet.</p>\n<p>Wamien: Tu viens de faire quoi?<br>\nÉléa: Je viens de griller le GPS qu’ils t’ont mis dans le poignet. Maintenant, tu dois partir.<br>\nWamien: Pour aller où? Tu ne... tu ne peux pas m’héberger quelques jours.<br>\nÉléa: Non désolé, je ne peux pas me permettre d’héberger quelqu’un, et j’ai mes raisons.<br>\nWamien: Ah, Heum, je vois. D’accord, et bien merci Éléa, prends soin de toi.<br>\nÉléa: Merci toi aussi Wamien, j’espère pouvoir te revoir un jour.</p>\n<p>Elle partit et je remis mes yeux en mode normal. Elle est étrange... On dirait qu’elle n’est pas une des expériences de Darakei, mais qui peut-elle bien être? Je repense un instant à Clara qui m’avait dit de revenir la voir si jamais il arrivait quelque chose. Pourtant, ça fait trois mois de ça. J’espère qu’elle se souvient de moi au moins. Je regarde le labo et repense à mes amis que je ne sais pas si je reverrai un jour.</p>\n<p>Une alarme se mise en marche. Ils avaient sûrement vu que j’avais disparu. Je me mets à courir vers le royaume voisin. Je passe à côté des gens et me mêle très facilement à la foule malgré mes simples vêtements. Je vais dans une cabine téléphonique, je sors le petit papier que Clara m’avait donné, je compose son numéro de téléphone et ça sonne.</p>\n<p>Clara: Allô?</p>"}},"chapitreAvant":"chapitre-9-la-cle-usb","chapitreApres":"chapitre-11-la-nouvelle-maison","nomRoman":"le-premier-cyborg-tome-1","slug":"chapitre-10-elea","codeChapitre":"H0001C010","equivalentUrl":"/stories/chapter/chapter-10-elea"}},"pathContext":{"slug":"chapitre-10-elea"}}

